@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&family=Roboto&family=Ubuntu:wght@300;400;500;700&display=swap');

body {
  font-family: 'Roboto', sans-serif;
}

.app {
  padding-top: 5rem;
  display: flex;
  justify-content: center;
}

.wrap {
  width: 500px;
}

$height: 46px;

.select {
  width: 100%;
  position: relative;
  z-index: 100;
  &.open {
    .select__dropdown {
      display: block;
    }
    .select__input {
      border-bottom: none;
    }
    .select__backdrop {
      display: block;
    }
  }

  &__input {
    border: 1px solid #ccc;
    border-radius: 5px;
    height: $height;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 1rem;
    cursor: pointer;
  }
  &__dropdown {
    display: none;
    position: absolute;
    top: $height;
    left: 0;
    right: 0;
    border: 1px solid #ccc;
    border-radius: 5px;
    max-height: 200px;
    overflow-y: auto;
    box-shadow: 2px 3px 5px rgba(0, 0, 0, 0.3);
  }
  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
  }
  &__backdrop {
    position: fixed;
    z-index: -1;
    display: none;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: transparent;
  }

  &__item {
    padding: 1rem;
    border-bottom: 1px solid #ccc;
    &.selected {
      background-color: bisque;
    }
    &:hover {
      background-color: #eee;
      cursor: pointer;
      transition: background-color ease-in 0.15s;
    }
  }
}
